.App {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    position: fixed;
    overflow: hidden;
    flex-direction: column;
    background-color: #131316;
}

main {
    gap: 40px;
    display: flex;
    margin: 0px auto;
    max-width: 1280px;
    height: calc(100% - 88px);
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

li {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    color: #fff;
}
